@import "../Styles/Colors.scss";

.page {
  // background-color: #fffdf5;
  min-height: 100vh;
  max-height: 100vh;
  overflow: auto;
  padding: 50px 0;
}

.split {
  display: flex;
}
@media only screen and (max-width: 700px) {
  .split {
    flex-wrap: wrap;
  }
}
.blob {
  width: 100%;
  padding: 10px;
  section {
    position: relative;
    z-index: 0;
    padding: 15px 20px;
    border-radius: 30px;
    max-width: 500px;
  }
}

.top {
  height: 300px;
}
